<template>
  <div>
      <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
              <div class="boll" v-show="flash" ref="ball"></div>
            </transition>
          
    <div class="mui-card">
	   <div class="mui-card-content">
         <mt-swipe :auto="4000">
        <mt-swipe-item><img src="static/photo_sharing-imgs/shouji.jpg"></mt-swipe-item>
        <mt-swipe-item><img src="static/photo_sharing-imgs/phone2.jpg"></mt-swipe-item>
        <mt-swipe-item><img src="static/photo_sharing-imgs/phone3.jpg"></mt-swipe-item>
      </mt-swipe>
     </div>
    </div>

    <div class="mui-card">
      <div class="mui-card-header">(shangyao) plus 16G 4G(shangyao) plus 16G 4G</div>
      <div class="mui-card-content">
        <div>
        <div class="price">
          <div><span class="old">1300</span></div>
          <div><span class="new">1200</span></div>
        </div>
        <div class="number">
            <div></div>
            <div class="mui-numbox"  data-numbox-min='1' >
              <button class="mui-btn mui-numbox-btn-minus" type="button">-</button>
              <input class="mui-numbox-input" type="number" value="1"/>
               
              <button class="mui-btn mui-numbox-btn-plus" type="button">+</button>
            </div>
        </div> 
        <mt-button type="primary" size="small"></mt-button>
        <mt-button type="danger" size="small" @click="addToShopCar"></mt-button>
        </div>
       
      </div>
    
      
    
    </div>

    <div class="mui-card">
	<div class="mui-card-header"></div>
	<div class="mui-card-content content">
    <div><div>2013564111</div>
      <div>60</div>
      <div>2018-09-05</div>
    </div>
  </div>
</div>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '',
      flash:false
    }
  },
  methods: {
    addToShopCar(){
      this.flash=! this.flash;
    },
    beforeEnter(el){
     el.style.transform="translate(0,0)";
    },
    enter(el, done){
     const balloffset=this.$refs.ball.getBoundingClientRect()
     const badeoffset=document.getElementById("bade").getBoundingClientRect()
      const xDist=badeoffset.left-balloffset.left;  
      const yDist=badeoffset.top-balloffset.top;

   
      el.offsetWidth;
      el.style.transform="translate("+xDist+"px,"+yDist+"px)";
      el.style.transition="all 1s cubic-bezier(.4,-0.3,1,.68)";
      done();
    },
    afterEnter(){
     this.flash=! this.flash;
    }
  },
}
</script>

<style scoped>
.mint-swipe{
  height: 12rem;
}
img{
 width: 50%;
 height: auto;
}
.mint-swipe-item{
  text-align: center;
}
.mui-card-content{
   padding: 10px 15px;
}
.new{
  font-size: 1rem;
  color: red;
}
.old{
  text-decoration: line-through;
}
.price{
 display: flex;
  font-size: 0.9rem;
  margin: 0.5rem 0;
}
.price>div:last-child{
 margin-left: 0.8rem;
}
.number{
   font-size: 0.9rem;
   display: flex;
   align-items: center;
   margin-bottom: 0.8rem;
}
.content{
   font-size: 0.9rem;
   color:gray;
}
.boll{
  position: absolute;
  z-index: 2000;
  border-radius:50%;
  background: red;
  width:1.1rem;
  height: 1.1rem;
  left: 9.1rem;
  top:24.3rem;
}
</style>



