
import Router from 'vue-router'
import home from './components/Home.vue'
import huiyuan from './components/huiyuan.vue'
import search from './components/search.vue'
import Shopping_Cart from './components/Shopping_Cart.vue'
import news from './components/News.vue'
import news_detail from './components/News_Detail.vue'
import photo_sharing from './components/Photo_Sharing.vue'
import photo_sharing_details from './components/Photo_Sharing_Details.vue'
import goods_list from './components/goods_list.vue'
import goods_detail from './components/goods_detail.vue'
const a =new Router({
    routes: [
        {
            path: '/',
            redirect: '/home',
            meta:{
                title:""
            }
        },
        {
            path:'/home',
            component:home,
            meta:{
                title:""
            }
        },
        {
            path:'/member',
            component:huiyuan,
            meta:{
                title:""
            }
        },
        {
            path:'/search',
            component:search,
            meta:{
                title:""
            }
        },
        {
            path:'/home/news',
            component:news,
            meta:{
                title:""
            }
        },
        {
            path:'/home/news_detail',
            component:news_detail,
            meta:{
                title:""
            }
        },
        {
            path:'/home/photo_sharing',
            component:photo_sharing,
            meta:{
                title:""
            }
        },
        {
            path:'/home/photo_sharing_details',
            component:photo_sharing_details,
            meta:{
                title:""
            }
        },
        {
            path:'/home/goods_list',
            component:goods_list,
            meta:{
                title:""
            }
        },
        {
            path:'/home/goods_detail',
            component:goods_detail,
            meta:{
                title:""
            }
        },
        {
            path:'/Shopping_Cart',
            component:Shopping_Cart,
            meta:{
                title:""
            }
        }
    ],
    linkActiveClass:'mui-active'
})

a.beforeEach((to,form,next)=>{
    document.title=to.matched[0].meta.title;
    next();
})
export default a;


