<template>
  <div class="contain">
    <div class="photo-header">
      <h4 class="title">1</h4>
      <div class="content">
        <span>2019-8-22 8:22</span>
         <span>2</span>
      </div>
    </div>
    <hr>
   <vue-preview :slides="slide1" @close="handleClose"></vue-preview>

  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
      slide1: [
          {
            src: 'static/photo_sharing-imgs/food1.jpg',
            msrc: 'static/photo_sharing-imgs/food1.jpg',
            alt: 'picture1',
            title: 'Image Caption 1',
            w: 300,
            h: 200
          },
          {
            src: 'static/photo_sharing-imgs/food2.jpg',
            msrc: 'static/photo_sharing-imgs/food2.jpg',
            alt: 'picture2',
            title: 'Image Caption 2',
            w: 300,
            h: 200
          }
        ]
    }
  },
  methods: {
      handleClose () {
        console.log('close event')
      }
    }
}
</script>

<style scoped>
.contain{
padding: 0 0.2rem;
}
 .photo-header{
   text-align: center;
 }
 .photo-header .title{
   color: #26a2ff;
   padding: 0.8rem;
   font-size: 1.3rem;
   }
    .photo-header .content{
     display: flex;
     justify-content: space-between;
     color:gray;
   }
  img{
        width: 100%!important;
  }
</style>



