<template>
  <div>
     <div class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
    <div class="mui-scroll">
        <a class="mui-control-item mui-active">
            
        </a>
        <a class="mui-control-item">
            
        </a>
        <a class="mui-control-item">
            
        </a>
        <a class="mui-control-item">
            
        </a>
        <a class="mui-control-item">
            
        </a>
        <a class="mui-control-item">
            
        </a>
    </div>
</div>

<ul class="ul-list">
        <router-link v-for="item in list" :key="item.id" tag="li" to="/home/photo_sharing_details">
            <img v-lazy="item.imgs">
            <div class="div-content">
              <h4 class="color-f">{{item.title}}</h4>
              <p class="color-f">{{item.content}}</p>
            </div>
        </router-link>
   </ul>
  </div>
</template>

<script>
import mui from '../assets/mui/js/mui.min.js'

export default {
  name: 'HelloWorld',
  data () {
    return {
      list: [{
        id: 1,
        title: '1',
        content: '',
        imgs: 'static/photo_sharing-imgs/timg1.jpg'
      },
      {
        id: 2,
        title: '1',
        content: '',
        imgs: 'static/photo_sharing-imgs/timg2.jpg'
      },
      {
        id: 3,
        title: '1',
        content: '',
        imgs: 'static/photo_sharing-imgs/timg3.jpg'
      },
      {
        id: 4,
        title: '1',
        content: '',
        imgs: 'static/photo_sharing-imgs/timg4.jpg'
      },
      {
        id: 5,
        title: '1',
        content: '',
        imgs: 'static/photo_sharing-imgs/timg5.jpg'
      },
      {
        id: 6,
        title: '1',
        content: '',
        imgs: 'static/photo_sharing-imgs/timg6.jpg'
      }

      ]
    }
  },
  mounted () {
    mui('.mui-scroll-wrapper').scroll({
      deceleration: 0.0005 // flick 0.0006
    })
  }
}
</script>

<style scoped>
 *{
     touch-action: pan-y;
 }
 .mui-active{
     border-bottom: none!important;
 }
 img{
    width: 100%;
    vertical-align: middle;
}
 img[lazy=loading] {
  width: 40px;
  height: 300px;
  margin: auto;
}

.ul-list{
    padding: 0;
    margin: 0;
}
.ul-list>li{
    list-style-type: none;
    margin:0 0.5rem;
    box-shadow: 0,0,6px #999;
    margin-top: 0.5rem;
    background: gray;
    position:relative;
    text-align: center;
}
.ul-list>li:last-child{
    margin-bottom:0;
}
.div-content{
  text-align: left;
  position:absolute;
  bottom:0;
  background: rgba(0,0,0,0.4);
  width: 100%;
  max-height: 5rem;
}
.color-f{
  color: #fff!important;
}
</style>



