<template>
  <div class="padding-top">
  <mt-header fixed :title="titleName" id="tarbarTitle">
   <span  slot="left" @click="back" v-show="bflag">
    <mt-button icon="back"></mt-button>
  </span>
  </mt-header>
  <transition>
  <router-view></router-view>
  </transition>

  <nav class="mui-bar mui-bar-tab">
  
    	<router-link class="mui-tab-item1" to="/home">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label"></span>
			</router-link>
      <router-link class="mui-tab-item1" to="/member">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label"></span>
			</router-link>
    	<router-link class="mui-tab-item1" to="/Shopping_Cart">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart" id="bade"><span class="mui-badge">9</span></span>
				<span class="mui-tab-label"></span>
      </router-link>
			<router-link class="mui-tab-item1" to="/search">
				<span class="mui-icon mui-icon-search"></span>
				<span class="mui-tab-label"></span>
			</router-link>
		</nav>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      bflag:false,
      titleName:''
    }
  },
  created(){
     this.titleName=document.title;
     if (this.$route.path==="/home") {
       this.bflag=false;
     }
      else{
         this.bflag=true;
      }
  },
methods: {
  back(){
    this.$router.go(-1);
  }
},
watch:{
  "$route.path":function(newval){
      this.titleName=document.title;
      if(newval==="/home"){
        this.bflag=false;
      }
      else{
         this.bflag=true;
      }
  }
}
}
</script>

<style scoped>
   .padding-top{
     padding-top:40px;
     padding-bottom: 50px;
     overflow-x: hidden;
     background: #ffffff;
     height: 100%;
   }
   .v-enter{
       opacity: 0;
     transform: translateX(100%);
   }
   .v-leave-to{
     opacity: 0;
     transform: translateX(-100%);
     position: absolute;
   }

   .v-enter-active,
   .v-leave-active{
     transition: all 0.4s ease;
   }

   .mui-bar-tab .mui-tab-item1.mui-active {
    color: #007aff;
}
.mui-bar-tab .mui-tab-item1 {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
.mui-bar-tab .mui-tab-item1 .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}
.mui-bar-tab .mui-tab-item1 .mui-icon ~ .mui-tab-label {
    font-size: 11px;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>



